body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.btn-sm{
    border-radius: .1rem;
    font-size: .7rem; 
}

.btn-primary {
    color: #F2F2F2;
    background-color: #135D84;
    border-color: #6A8093;
}
.showFoods h5{
    color:#6A8093;
    margin-bottom: 0rem;
    font-size: 17px;
}


hr { 
    border: 0; 
    height: 1px; 
    margin: 0;
    background-image: linear-gradient(to left, #f0f0f0, #8c8b8b, #f0f0f0);
  }
@media (min-width: 768px){
    .container{
        max-width: 900px;
    }
    .hiden{
        display: none;
    }
    .hiden1{
        display: block;
        
    }
    .section3{
        background-color: #00687A;
        padding: 15px 0;
    }
    
    .section3 h4{
        font-size: 25px;
        margin-top: .4rem;
        color: #F2F2F2;
    }
    
    .section4 .orders{
        background-color: #fff;
        border: 0.1px solid #4D5057;
        box-shadow: 2px 2px 2px 2px #6A8093;
        padding: 20px 5px;
    }
    
    .section4 .titleOrder{
        font-size: 40px;
    }
    .section4 .titleOrder p{
        margin: 10px 0;
    }
    .filaContent{
        margin: 0;
    }
    
    .filaContent .orderList{
        background-color: #6A8093;
        border:1px solid #6A8093;
        padding: 2px;
    }
    
    .listFood .food{
        display: inline-block;
        font-size: 13px;
        color:#fff;
    }
    .listFood  .name{
        width: 70%;
    }
    .listFood  .price{
        width: 15%;
    }
    .listFood  .icono{
        width: 15%;
    }
    .orderList .priceTotal{
        color:#fff; 
    }
    
}


